import { TodoBoardBox } from './styled';
import TodoItem from './TodoItem';

function TodoBoard({ todoItems, setTodoItems, selectCg, setSelectCg }) {
  const filterCategory =
    selectCg == '' ? todoItems : todoItems.filter((item) => item.category == selectCg);
  return (
    <TodoBoardBox>
      {todoItems.length > 0 ? (
        <select
          value={selectCg}
          onChange={(e) => {
            setSelectCg(e.target.value);
          }}
        >
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
          <option value=""></option>
        </select>
      ) : (
        <span className="nothing">  </span>
      )}
      <ul>
        {filterCategory.map((todoItem) => (
          <TodoItem
            key={todoItem.id}
            todoItem={todoItem}
            todoItems={todoItems}
            setTodoItems={setTodoItems}
          />
        ))}
      </ul>
    </TodoBoardBox>
  );
}

export default TodoBoard;
